import {Injectable} from '@angular/core';

import {MemberprofModel} from "../../models/memberprof-model";
import {Api} from "../api/api";
import {LocalStorageProvider} from "../local-storage/local-storage";
import {FunctionsProvider} from "../functions/functions";
import {Platform} from "ionic-angular";

@Injectable()
export class MembersProf {

    constructor(public api: Api,
                private func: FunctionsProvider,
                private storage: LocalStorageProvider,
               private platform:Platform
    ) { }

    query(uid: any) {
        return this.storage.get(this.api.churchname+'user'+uid);
    }
    query_family(uid: any) {
        return this.storage.get(this.api.churchname+'family'+uid);
    }


    qiri(params: any) {
        return this.api.get('user/'+ params);

    }

  add(member: MemberprofModel) {

      var headers = new Headers().set('Content-Type','application/json');


      return this.api.post('user', member, headers);
  }

    addfamily(member: MemberprofModel) {

        var headers = new Headers().set('Content-Type','application/json');


        return this.api.post('familymembers', member, headers);
    }


    updateinfo(member: MemberprofModel,id:any) {

        var headers = new Headers().set('Content-Type','application/json');

        return this.api.put('profile/'+id, member, headers);

    }


}



