import {Component} from "@angular/core";
import {Alert, AlertController, NavParams, Platform, ViewController} from "ionic-angular";
import {CallNumber} from "@ionic-native/call-number";


@Component({
  selector: 'modal-give-page',
  templateUrl: 'modal-content-give.html'
})
export class ModalGive {

        model: any = {};
  private option: any = "1";
  private amountOption: any ={};
  private eco: string;

        constructor(
        public platform: Platform,
        public params: NavParams,
        private callNumber: CallNumber,
        public viewCtrl: ViewController,
        private alertCtrl: AlertController

) {


  }
callPhone(){
          this.eco = "*151*2*1*31507*"+this.amountOption+"*"+ this.option + encodeURI("#");
          console.log(this.eco)
           this.callNumber.callNumber(this.eco, true)

    .then(res => {
        console.log('Launched dialer!', res)
    })
    .catch(err => {
        this.presentAlert("Error! Access Denied please check your Call/Dialer permissions")
    });
}

    presentAlert(id) {
        let alert: Alert = this.alertCtrl.create({
            title: id,

            buttons: ['Dismiss']
        });
        alert.present();
    }


  dismiss() {
    this.viewCtrl.dismiss();
  }
}



