import {Component, ViewEncapsulation} from '@angular/core';
import {
    Alert,
    AlertController,
    LoadingController,
    NavParams,
    Platform,
    ToastController,
    ViewController
} from "ionic-angular";
import {AngularFireAuth} from '@angular/fire/auth'

import {Storage} from "@ionic/storage";
import {MaritalProvider} from "../../../providers/memberProf/marital";
import {GroupsProvider} from "../../../providers/memberProf/groups";
import {FunctionsProvider} from "../../../providers/functions/functions";
import {MembersProf} from "../../../providers/memberProf/members-prof";
import {LangsProvider} from "../../../providers/memberProf/langs";


@Component({
  selector: 'page-about',
  templateUrl: 'edit.html',
  encapsulation: ViewEncapsulation.None
})
export class ModalEdit {


    private userinfo: any ={};

  model: any = {};
    private uid: string;
    private id: any;
    private groups: any;
    private maritals: any;
    private langs: any;

    private dp: any;
    private ecostatuses: any;

  constructor(
    public platform: Platform,
    public params: NavParams,
    public viewCtrl: ViewController,
    private afs: AngularFireAuth,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private member: MembersProf,
    private func :FunctionsProvider,
    private sqlstorage: Storage,
    private group : GroupsProvider,
    private marital : MaritalProvider,
    private lang : LangsProvider,
    private user: MembersProf,


  ) {

      this.loadData()


      this.group.query().then((res: any) => {
          this.groups = JSON.parse(res.data);
      })
      this.marital.query().then((res: any) => {
          this.maritals = JSON.parse(res.data)
      });
      this.lang.query().then((res: any) => {
          this.langs = JSON.parse(res.data)
      });
      this.marital.ecoquery().then((res: any) => {
          this.ecostatuses = JSON.parse(res.data)
      });


      this.sqlstorage.get('prof_pic').then((val) => {
          this.dp = val;
      })
  }



    loadData() {

        this.afs.authState.subscribe(res => {
            if (res && res.uid) {
                this.uid = res.uid
                this.func.sync(res.uid,"1")
                this.user.query(res.uid).then((res: any) => {
                    this.userinfo = JSON.parse(res.data)
                    this.id = res.data.id

                }).catch(er => console.log(er.message))
            } else {
            }
        });


    }
   async save(inf: any){


       let postData = {
           first_name: this.userinfo.first_name,
           last_name: this.userinfo.last_name,
           cell_number: this.userinfo.cell_number,
           group: this.userinfo.group,
           marital_status_id: this.userinfo.marital_status_id,
           address: this.userinfo.address,
           date_of_birth: this.userinfo.date_of_birth,
           gender: this.userinfo.gender,
           languages_id: this.userinfo.languages_id,
           economic_status_id: this.userinfo.economic_status_id,
           prof_pic: this.dp
           //current_state_id
           //status_id
       };



        const loader = this.loadingCtrl.create({
            content: "Please Wait",
            duration:10000
        });
        loader.present();
            console.log("userrrr"+ (JSON.stringify(postData)))
             this.member.updateinfo(postData,this.userinfo.id).then((res: any) => {

                this.presentToast();
                this.func.sync(this.uid,"1")
               this.dismiss()

                loader.dismissAll()
            }, err => {
                this.presentAlert("Please Check your details and try again")
              loader.dismissAll()
                console.log(JSON.stringify(err));
            });

        }


    presentAlert(er) {
        let alert: Alert = this.alertCtrl.create({
            title: er,

            buttons: ['Dismiss']
        });
        alert.present();
    }

    presentToast() {
        let toast = this.toastCtrl.create({
            message: 'Info Updated !',
            duration: 3000,
            position: 'top'

        });

        toast.present();
    }




  dismiss() {
    this.viewCtrl.dismiss();
    this.func.sync(this.uid,"1")
  }
}






