import {Injectable} from '@angular/core';
import {Api} from '../api/api';
import {Storage} from '@ionic/storage';

@Injectable()
export class FunctionsProvider {

  constructor( private storage: Storage, private api: Api) {

  }

  sync(uid:any ,bid:any)
  {
    console.log('Syncing : .....');

    // Push local unsysnced data first


    let synced: any;
    let unsynced : any;


    // Get members and reshresh local db
    this.api.get('devotions/'+bid)
    .then(res => {
      this.storage.set(this.api.churchname+'devotions', res);
    }).catch( error => {
      console.log('ERROR : dev ' )
    });

      this.api.get('user/'+uid)
          .then(res => {
              this.storage.set(this.api.churchname+'user'+uid, res);
          }).catch( error => {
          console.log('ERROR : user' )
      });

      this.api.get('familymembers/'+uid)
          .then(res => {
              this.storage.set(this.api.churchname+'family'+uid, res);
          }).catch( error => {
          console.log('ERROR : user' )
      });

    // Get visitors and reshresh db
    this.api.get('groups')
    .then(res => {

        this.storage.set(this.api.churchname+'groups', res);
    }).catch( error => {
      console.log('ERROR : ' )
    });



      this.api.get('languages')
          .then(res => {

              this.storage.set(this.api.churchname+'langs', res);
          }).catch( error => {
          console.log('ERROR :  langs' )
      });

      this.api.get('maritalstatuses')
          .then(res => {

              this.storage.set(this.api.churchname+'maritalstatuses', res);
          }).catch( error => {
          console.log('ERROR : ' )
      });


      this.api.get('economicstatuses')
          .then(res => {

              this.storage.set(this.api.churchname+'ecostatus', res);
          }).catch( error => {
          console.log('ERROR : ' )
      });


      this.api.get('events/local/'+bid)
          .then(res => {

              this.storage.set(this.api.churchname+'localevents', res);
          }).catch( error => {
          console.log('ERROR : ' )
      });

      this.api.get('events/regional/'+bid)
          .then(res => {
              this.storage.set(this.api.churchname+'regionalevents', res);
          }).catch( error => {
          console.log('ERROR : ' )

      }); this.api.get('events/national/'+bid)
      .then(res => {
          this.storage.set(this.api.churchname+'nationalevents', res);
      }).catch( error => {
          //console.log('ERROR nt events: ' )
      });

     // Get converts and reshresh db
     this.api.get('notices/'+bid)
     .then(res => {
       this.storage.set(this.api.churchname+'notices', res);
     }).catch( error => {
       console.log('ERROR nitic: ' )
     });

     // Get activities and reshresh db
     this.api.get('cellgroups/'+bid)
     .then(res => {
       this.storage.set(this.api.churchname+'cellgroups', res);
     }).catch( error => {
       console.log('ERROR cell : ' )
     });

     // Get expenses and reshresh db


     // Get collectives and reshresh db
     this.api.get('generalinfo/'+bid)
     .then(res => {
       this.storage.set(this.api.churchname+'general' ,res);
     }).catch( error => {
       console.log('ERROR: ginfo' )
     });


    //  Load Graph data
    this.api.get('pledges/types/'+uid)
    .then(res => {
      this.storage.set(this.api.churchname+'projects'+uid, res);
    })
    .catch( error => {
      console.log('ERROR: projects' )
    });





      this.api.get('pledges/'+uid)
          .then(res => {
              this.storage.set(this.api.churchname+'pledges'+uid, res);
          })
          .catch( error => {
              console.log('ERROR  pledge: ' )
          });




  }

}



