import {Injectable} from '@angular/core';

import {Api} from '..';
import {LocalStorageProvider} from "../local-storage/local-storage";
import {FunctionsProvider} from "../functions/functions";

@Injectable()
export class EventProvider {


  // query(type: any, id: any) {
  //   return this.api.get('events/' + type +"/" +id);
  // }
    constructor(public api: Api, private func: FunctionsProvider, private storage: LocalStorageProvider) { }

    query(params?: any) {
        return this.storage.get(this.api.churchname+'localevents');
    }


}



