import {Component} from "@angular/core";
import {LoadingController, NavController, NavParams, Platform, ViewController} from "ionic-angular";
import {AngularFireAuth} from "@angular/fire/auth";
import {AngularFirestore, AngularFirestoreCollection} from "@angular/fire/firestore";
import {Observable} from "rxjs-compat";
import * as moment from "moment";
import {MembersProf} from "../../providers/memberProf/members-prof";
import {Api} from "../../providers/api/api";
import {Storage} from "@ionic/storage";

interface Prayer {
  id: string;
  title: string,
  message: string,
  pid: number

}


@Component({
  selector: 'prayer-page',
  templateUrl: 'modalcontent.html'
})
export class ModalContentPage {

        private db: any;
        model: any = {};
  private dtt = moment().format('YYYY-MM-DD HH:mm');
  pid = Date.now();


        private messages: any;
        prayers: Observable<Prayer[]>;
        prayersCollectionRef: AngularFirestoreCollection<Prayer>;
    private uid: string;
    private title: any;
    private message: string;
    private member: any;
    private userinfo: any ={};
    private dp: any;
        constructor(
        public platform: Platform,
        private churchname: Api,
        private navCtrl: NavController,
      public params: NavParams,
        public viewCtrl: ViewController,
        public loading:LoadingController,
        public afAuth: AngularFireAuth,
        public afs: AngularFirestore,
        private user: MembersProf,
        private sqlstorage:Storage

) {
            this.checkAuth()
            this.loadData()
            this.sqlstorage.get('prof_pic').then((val) => {
                this.dp = val

            });


  }






  async loadData(){
      this.prayersCollectionRef = this.afs.collection(this.churchname.churchname)
          .doc("cms").collection("prayers");



  }

   async checkAuth () {
        this.afAuth.authState.subscribe(res => {
            if (res && res.uid) {
                this.uid = res.uid

                this.user.query(res.uid).then((res: any) => {
                    this.userinfo = JSON.parse(res.data)

                }).catch(er => console.log(er))
                ;
            } else {
                this.navCtrl.setRoot('login',{
                    refresh:"true",
                });
            }
        });
    }
 async addocument() {
            if (this.title && this.message) {

                let content: any = {
                    title: this.title,
                    uid: this.userinfo.uid,
                    id: this.pid,
                    date: this.dtt,
                    member: this.userinfo.first_name +" "+ this.userinfo.last_name,
                    message: this.message,
                   image: this.dp || null
                }
                const loader = this.loading.create({
                    content: "Please wait...",
                    duration: 10000,
                });
                loader.present();
                console.log();
                loader.dismissAll();
                this.dismiss();


                this.prayersCollectionRef.add(content)
                    .then(() => {
                        console.log(content)
                        loader.dismissAll();
                        this.dismiss();
                    }).catch((error: any) => {
                    console.log(error);
                    loader.dismissAll();
                    this.dismiss();
                });
            }
  }


  dismiss() {
    this.viewCtrl.dismiss();
  }
}



