import {Component, ViewEncapsulation} from '@angular/core';
import {
    Alert,
    AlertController,
    LoadingController,
    NavController,
    NavParams,
    Platform,
    ToastController,
    ViewController
} from "ionic-angular";
import {Storage} from '@ionic/storage';
import {MembersProf} from "../../providers/memberProf/members-prof";
import {MaritalProvider} from "../../providers/memberProf/marital";
import {GroupsProvider} from "../../providers/memberProf/groups";


@Component({
  selector: 'page-about',
  templateUrl: 'complete.html',
  encapsulation: ViewEncapsulation.None
})
export class ModalComplete {
  dob = '1990-05-17';
  gender = 'Male';
  group = '';
  status ='';
  cellgroup_id = '';
  departments = '';
  name = '';
  address = '';
  surname = '';
  uid = '';
  branch_id = '';
  phone = '';
  email = 'None';
    maritals: any ;
    groups: any;



    model: any = {};
    private fullname: string;

  constructor(
    public platform: Platform,
    public params: NavParams,
    public viewCtrl: ViewController,
    private storage: Storage,
    private alertCtrl:AlertController,
    private loading:LoadingController,
    private toastCtrl: ToastController,
    private  navCtrl: NavController,
    private member: MembersProf,
    private marital:MaritalProvider,
    private groupp:GroupsProvider,

  ) {

      this.branch_id = params.get('bid');
      this.cellgroup_id = params.get('cell');
      this.departments = params.get('dptid');
      this.uid = params.get('uid');
      this.phone = params.get('phone');

      this.groupp.livequery().then((res: any) => {
          this.groups = JSON.parse(res.data);
      });
      this.marital.livequery().then((res: any) => {
          this.maritals = JSON.parse(res.data);
      });

  }

async save(){
    const loader = this.loading.create({
        content: "Please Wait",
        duration:10000
    });
    loader.present();
      this.fullname = this.name +" "+ this.surname;

    let postData = {
        branch_id: this.branch_id,
        name: this.fullname,
        uid: this.uid,
        email: this.email,
        phone: this.phone,
        group: this.group,
        marital_status: this.status,
        address: this.address,
        departments: this.departments,
        dob: this.dob,
        gender: this.gender,
        cellgroup_id: this.cellgroup_id
    }

        console.log(postData)
            await this.member.add(postData)
                .then((res: any) => {
                    this.presentToast();
                    this.goToHome()
                    this.dismiss()
                    loader.dismissAll()
                }, err => {
                    this.presentAlert("Please Check your details and try again")
                   loader.dismissAll()
                    console.log("Error while a");
                });

        }





    presentAlert(er) {
        let alert: Alert = this.alertCtrl.create({
            title: er,

            buttons: ['Dismiss']
        });
        alert.present();
    }

    presentToast() {
        let toast = this.toastCtrl.create({
            message: 'Hooray Thank you for registering !',
            duration: 3000,
            position: 'top'

        });

        toast.present();
    }
//    async saveData(){
//         this.storage.set('user',
//         )
//     .then(
//       () => {
//           console.log('Stored item!')
//           this.goToHome()
//           this.dismiss()
//
//       }
//
//
//     );
//
// }
  dismiss() {
    this.viewCtrl.dismiss();
  }

  goToHome(){
      this.storage.set('login', this.uid+'true');

      this.navCtrl.setRoot('HomePage',

          {
              uid:this.uid,
              bid: this.branch_id,
              refresh:'true',

      },{
              direction: "forward",
              animate:true,

          }


      )
  }}








