import {Injectable} from '@angular/core';

import {Api} from '..';
import {LocalStorageProvider} from "../local-storage/local-storage";
import {FunctionsProvider} from "../functions/functions";
import {ProjectsModel} from "../../models/projects-model";

@Injectable()
export class ProjectsProvider {
    constructor(public api: Api, private func: FunctionsProvider, private storage: LocalStorageProvider) { }

    query(uid: any) {
        return this.storage.get(this.api.churchname+'projects'+uid);
    }
    add(projects: ProjectsModel) {
        var headers = new Headers().set('Content-Type','application/json');

        return this.api.post('pledges', projects, headers);
    }

}



