import {Component, ViewEncapsulation} from '@angular/core';
import {
    Alert,
    AlertController,
    LoadingController,
    NavParams,
    Platform,
    ToastController,
    ViewController
} from "ionic-angular";
import {AngularFireAuth} from '@angular/fire/auth'

import {Storage} from "@ionic/storage";
import {MembersProf} from "../../../../providers/memberProf/members-prof";
import {FunctionsProvider} from "../../../../providers/functions/functions";


@Component({
  selector: 'page-family-add',
  templateUrl: 'add-family.html',
  encapsulation: ViewEncapsulation.None
})
export class ModalAddFamily {


    private familyinfo: any ={};

  model: any = {};
    private uid: string;
    private id: any;


  constructor(
    public platform: Platform,
    public params: NavParams,
    public viewCtrl: ViewController,
    private afs: AngularFireAuth,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private familymember: MembersProf,
    private func :FunctionsProvider,
    private sqlstorage: Storage,




    private family: MembersProf,


  ) {

      this.afs.authState.subscribe(res => {
          if (res && res.uid) {
              this.uid = res.uid}})





  }



  async save(inf: any){


       let postData = {
          first_name: this.familyinfo.name,
          last_name: this.familyinfo.last_name,
           uid: this.uid,
           relation:this.familyinfo.relation,
           contact: this.familyinfo.contact

       }

        const loader = this.loadingCtrl.create({
            content: "Please Wait",
            duration:10000
        });
        loader.present();
            console.log("familyrrr"+ (postData))
             this.familymember.addfamily(postData).then((res: any) => {

                this.presentToast();
               //  this.func.sync(this.uid,"1")
               this.dismiss()

                loader.dismissAll()
            }, err => {
                this.presentAlert("Please Check your details and try again")
              loader.dismissAll()
                console.log(err);
            });

        }


    presentAlert(er) {
        let alert: Alert = this.alertCtrl.create({
            title: er,

            buttons: ['Dismiss']
        });
        alert.present();
    }

    presentToast() {
        let toast = this.toastCtrl.create({
            message: 'Info Updated !',
            duration: 3000,
            position: 'top'

        });

        toast.present();
    }




  dismiss() {
    this.viewCtrl.dismiss();
  }
}






