import {Injectable} from '@angular/core';

import {MemberprofModel} from "../../models/memberprof-model";
import {Api} from "../api/api";
import {LocalStorageProvider} from "../local-storage/local-storage";
import {FunctionsProvider} from "../functions/functions";
import {Platform} from "ionic-angular";

@Injectable()
export class MaritalProvider {

    constructor(public api: Api, private func: FunctionsProvider,
                private storage: LocalStorageProvider,
               private platform:Platform
    ) { }

    query(params?: any) {
        return this.storage.get(this.api.churchname+'maritalstatuses');
    }
    ecoquery(params?: any) {
        return this.storage.get(this.api.churchname+'ecostatus');
    }


    livequery(params?: any) {
        return this.api.get('maritalstatuses', params);
    }



}



