import {Component} from "@angular/core";
import {
    Alert, AlertController, LoadingController, NavController, NavParams, Platform, ToastController,
    ViewController
} from "ionic-angular";
import {ProjectsProvider} from "../../../providers/projects/projects-provider";
import {AngularFireAuth} from '@angular/fire/auth';


@Component({
  selector: 'modal-content-pledge-page',
  templateUrl: 'modal-content-pledge.html'
})
export class ModalProjects {

  private date: any ;
  private amount: any ;
  private projectId: any;
    private uid: any;

        constructor(
        public platform: Platform,
        public navCtrl: NavController,
        public params: NavParams,
        public viewCtrl: ViewController,
        private  alertCtrl: AlertController,
        private toastCtrl: ToastController,
        private afs: AngularFireAuth,
        private pledg: ProjectsProvider,
        private loading: LoadingController,

) {
         this.projectId = params.get('id');
          console.log(this.projectId.id);
            this.afs.authState.subscribe(res => {
                if (res && res.uid) {
                this.uid = res.uid

                } else {
                    this.navCtrl.setRoot('login',{
                        refresh:"true",
                    });
                }
            });

  }


  pledge(){

      const loader = this.loading.create({
          content: "Please Wait",
          duration: 10000
      });
      loader.present()
      let postData ={
    "amount":this.amount,
    "uid":this.uid,
    "date":this.date,
    "pledge_type_id":this.projectId.id
}
      if(this.amount || this.date || this.uid){
      this.pledg.add(postData)
          .then((res: any) => {
              this.presentToast();
              loader.dismissAll()
          }, err => {
          loader.dismissAll()
          this.presentAlert("Please try again")
              console.log("Error while adding member");
          });






                    }

 else {
      this.presentAlert("Fields cannot be Empty")
            }


  }
    presentAlert(er) {
        let alert: Alert = this.alertCtrl.create({
            title: er,

            buttons: ['Dismiss']
        });
        alert.present();
    }

presentToast() {
    let toast = this.toastCtrl.create({
        message: 'Pledge added !',
        duration: 3000,
        position: 'top'

    });

    toast.present();
}

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



