///<reference path="../providers/fcm/fcm.ts"/>
import {Component, ViewChild,} from '@angular/core';
import {StatusBar} from '@ionic-native/status-bar';
import {TranslateService} from '@ngx-translate/core';
import {Config, Nav, Platform, ToastController} from 'ionic-angular';
import {FcmProvider} from "../providers/fcm/fcm";
import {Home, Login} from "../pages";
import {Storage} from "@ionic/storage";
import {AngularFireAuth} from '@angular/fire/auth';


@Component({
  template: `<ion-nav #content [root]="rootPage"></ion-nav>`
})
export class MyApp {
   rootPage = '';
    private logged: any;
   //private uid: any;

  @ViewChild(Nav) nav: Nav;
    private uid: any;




  constructor(private translate: TranslateService, platform: Platform,
            private config: Config,
             private storage: Storage,
             private statusBar: StatusBar,
              private fcm: FcmProvider,
              toastCtrl: ToastController,
              private afs: AngularFireAuth,
  ) {
    platform.ready().then(() => {

        this.afs.authState.subscribe(res => {
           // this.uid = res.uid;

            if (res && res.uid ) {
         this.rootPage = Home

            }
        else {
                this.rootPage = Login
            }
        })

        //  func.sync()
         // Get a FCM token
          fcm.getToken()
          fcm.subscribeToTopic('BCC').catch(err=>{

          });
          fcm.subscribeToTopic('BCC-prayers').catch(err=>{
              console.log(JSON.stringify(err))

          });


          fcm.listenToNotifications()
              .subscribe(res => {

                  if(res.wasTapped){
                     this.nav.setRoot('HomePage', { pageId: res.title });

                  }
                  else {
                      const toast = toastCtrl.create({
                          message: res.body,
                          duration: 6000,
                          showCloseButton: true,
                          closeButtonText: 'View',
                          dismissOnPageChange: true,


                      });
                      toast.present();

                      toast.onDidDismiss((data, role) => {
                         // console.log('Dismissed toast');
                          if (role == "close") {
                              this.nav.push('HomePage', { pageId: res.title });


                          }
                      });
                  }
              })






      });
        statusBar.styleDefault();
        this.statusBar.backgroundColorByHexString('#0e57f7')
    this.initTranslate();


  }

  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('en');
    const browserLang = this.translate.getBrowserLang();

    if (browserLang) {
      if (browserLang === 'zh') {
        const browserCultureLang = this.translate.getBrowserCultureLang();

        if (browserCultureLang.match(/-CN|CHS|Hans/i)) {
          this.translate.use('zh-cmn-Hans');
        } else if (browserCultureLang.match(/-TW|CHT|Hant/i)) {
          this.translate.use('zh-cmn-Hant');
        }
      } else {
        this.translate.use(this.translate.getBrowserLang());
      }
    } else {
      this.translate.use('en'); // Set your language here
    }

    this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
      this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    });
  }


}



