import {Component} from "@angular/core";
import {Alert, AlertController, NavController, NavParams, Platform, ViewController} from "ionic-angular";
import {SocialSharing} from "@ionic-native/social-sharing";
import {InAppBrowser} from "@ionic-native/in-app-browser";
import {AngularFireAuth} from "@angular/fire/auth";


@Component({
  selector: 'modal-options',
  templateUrl: 'modal-content-options.html'
})
export class ModalOption {



        constructor(
        public platform: Platform,
        public params: NavParams,
        public viewCtrl: ViewController,
        private alertCtrl: AlertController,
        private socialSharing: SocialSharing,
        private navCtrl: NavController,
      //  private storage: Sto
        private iab:InAppBrowser,
        private afs: AngularFireAuth,

) {


  }

  share(){
            this.socialSharing.shareViaWhatsApp(" Hello there Download Our BCC Faith Ministries App on Playstore => https://play.google.com/store/apps/details?id=com.churchapp.disciplesoft.bcc  " +
                " and or on AppleStore => https://itunes.apple.com/us/app/bcc-faith-ministries/id1440602772?ls=1&mt=8")

  }
  logout(){

      this.afs.auth.signOut().then(() => {

          this.navCtrl.setRoot('login', {
              refresh: "true",
          });
      });
  }

  aboutdev(){
      const browser = this.iab.create(
          'https://disciplesoft.co.za','_blank');
      browser.show()
  }

    presentAlert(id) {
        let alert: Alert = this.alertCtrl.create({
            title: id,

            buttons: ['Dismiss']
        });
        alert.present();
    }


  dismiss() {
    this.viewCtrl.dismiss();
  }
}



